import React from 'react'
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from '../ui/dialog'
import { Button } from '../ui/button'
import { useWebAuth } from '../../hooks/useWebAuth'
import { Loader2, ExternalLink, AlertCircle, Wifi, WifiOff } from 'lucide-react'

interface WebAuthDialogProps {
  open: boolean
  onOpenChange: (open: boolean) => void
}

export function WebAuthDialog({ open, onOpenChange }: WebAuthDialogProps) {
  const {
    authenticated,
    user,
    loading,
    error,
    connectionStatus,
    login,
    logout,
    checkConnection,
  } = useWebAuth()

  const handleLogin = async () => {
    const result = await login()
    if (result.success) {
      // 
      // onOpenChange(false)
    }
  }

  const handleLogout = async () => {
    await logout()
  }

  const handleRetryConnection = async () => {
    await checkConnection()
  }

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-[425px]">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
            <ExternalLink className="h-5 w-5" />
            Web
          </DialogTitle>
          <DialogDescription>
            WebAI
          </DialogDescription>
        </DialogHeader>

        <div className="py-4 space-y-4">
          {/*  */}
          <div className="flex items-center gap-2 p-3 bg-gray-50 rounded-lg">
            {connectionStatus.checking ? (
              <Loader2 className="h-4 w-4 animate-spin" />
            ) : connectionStatus.connected ? (
              <Wifi className="h-4 w-4 text-green-600" />
            ) : (
              <WifiOff className="h-4 w-4 text-red-600" />
            )}
            <span className="text-sm font-medium">
              Web: {' '}
              {connectionStatus.checking
                ? '...'
                : connectionStatus.connected
                ? ''
                : ''
              }
            </span>
          </div>

          {/*  */}
          {connectionStatus.connected && (
            <div className="space-y-3">
              {loading ? (
                <div className="flex items-center justify-center py-8">
                  <Loader2 className="h-6 w-6 animate-spin mr-2" />
                  <span>...</span>
                </div>
              ) : authenticated ? (
                <div className="space-y-3">
                  <div className="flex items-center gap-2 p-3 bg-green-50 border border-green-200 rounded-lg">
                    <div className="h-2 w-2 bg-green-500 rounded-full"></div>
                    <span className="text-sm font-medium text-green-800">
                      : {user?.username}
                    </span>
                  </div>
                  
                  <div className="text-sm text-gray-600">
                    <p>Web</p>
                    <ul className="mt-2 space-y-1 list-disc list-inside">
                      <li>AI</li>
                      <li></li>
                      <li></li>
                    </ul>
                  </div>
                </div>
              ) : (
                <div className="space-y-3">
                  <div className="flex items-center gap-2 p-3 bg-yellow-50 border border-yellow-200 rounded-lg">
                    <AlertCircle className="h-4 w-4 text-yellow-600" />
                    <span className="text-sm font-medium text-yellow-800">
                      Web
                    </span>
                  </div>
                  
                  <div className="text-sm text-gray-600">
                    <p>Web</p>
                    <p className="mt-1"></p>
                  </div>
                </div>
              )}

              {/*  */}
              {error && (
                <div className="p-3 bg-red-50 border border-red-200 rounded-lg">
                  <div className="flex items-center gap-2">
                    <AlertCircle className="h-4 w-4 text-red-600" />
                    <span className="text-sm font-medium text-red-800"></span>
                  </div>
                  <p className="text-sm text-red-700 mt-1">{error}</p>
                </div>
              )}
            </div>
          )}

          {/*  */}
          {!connectionStatus.connected && !connectionStatus.checking && (
            <div className="space-y-3">
              <div className="p-3 bg-red-50 border border-red-200 rounded-lg">
                <div className="flex items-center gap-2">
                  <AlertCircle className="h-4 w-4 text-red-600" />
                  <span className="text-sm font-medium text-red-800">Web</span>
                </div>
                <p className="text-sm text-red-700 mt-1">
                  Web
                </p>
              </div>
              
              <div className="text-sm text-gray-600">
                <p className="font-medium">Web</p>
                <ol className="mt-2 space-y-1 list-decimal list-inside">
                  <li></li>
                  <li>web</li>
                  <li>: <code className="bg-gray-100 px-1 rounded">npm run dev</code></li>
                  <li>: <code className="bg-gray-100 px-1 rounded">.bat</code></li>
                </ol>
              </div>
            </div>
          )}
        </div>

        <DialogFooter className="flex justify-between">
          <div className="flex gap-2">
            {!connectionStatus.connected && (
              <Button 
                variant="outline" 
                onClick={handleRetryConnection}
                disabled={connectionStatus.checking}
              >
                {connectionStatus.checking ? (
                  <Loader2 className="h-4 w-4 animate-spin mr-2" />
                ) : null}
                
              </Button>
            )}
          </div>
          
          <div className="flex gap-2">
            {connectionStatus.connected && (
              <>
                {authenticated ? (
                  <Button 
                    variant="outline" 
                    onClick={handleLogout}
                    disabled={loading}
                  >
                    {loading ? (
                      <Loader2 className="h-4 w-4 animate-spin mr-2" />
                    ) : null}
                    
                  </Button>
                ) : (
                  <Button 
                    onClick={handleLogin}
                    disabled={loading}
                  >
                    {loading ? (
                      <Loader2 className="h-4 w-4 animate-spin mr-2" />
                    ) : (
                      <ExternalLink className="h-4 w-4 mr-2" />
                    )}
                    Web
                  </Button>
                )}
              </>
            )}
            
            <Button variant="outline" onClick={() => onOpenChange(false)}>
              
            </Button>
          </div>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  )
} 